.navs {
    margin: 0;
    display: flex;
    p.nav_item {
        width: $navsWidth;
        height: $navsHeight;
        line-height: $navsHeight;
        font-size: $IconSize;
        flex: 1;
        text-align: center;
        margin: 0 0 0$MarginLeft;
        cursor: pointer;
    }
    p.nav_item:first-child {
        margin: 0;
    }
}
