
// global
$color: #f3f3f3 !default;
$background: #000 !default;
$width: 100% !default;
$height: 100% !default;
$maxWidth: 1060px !default;

// font size
$fontSize: 14px !default;
$fontSize-h1: $fontSize * 2.75 !default;
$fontSize-h2: $fontSize * 2 !default;
$fontSize-h4: $fontSize * 1.5 !default;

// layout
$headerHeight: 40px;
$tabHeight: 32px;
$footerHeight: 40px;

// Navs
$navsWidth: 32px;
$navsHeight: 32px;
$IconSize: 24px;
$MarginLeft: 10px;

// Section
$sectionBoxMaxWidth: 800px;
$autoColor: #757575;
