header.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: $headerHeight;
    line-height: $headerHeight;
}

section.section {
    height: calc(100vh - #{$headerHeight} - #{$tabHeight}  - #{$footerHeight});
    overflow-y: auto;
}

footer.footer {
    height: $footerHeight;
    line-height: $footerHeight;
    text-align: center;
}
